
































import { Component, Vue } from 'vue-property-decorator';
import { State, Getter } from 'vuex-class';
import { IActivityItem } from '@/types/interface';
import banner from '../../components/Banner.vue';

function openWindow(name: string) {
  window.open('about:blank', name);
}

function openPostWindow(url: string, id: string, name: string) {
  const tempForm = document.createElement('form');
  tempForm.id = 'tempForm1';
  tempForm.method = 'post';
  tempForm.action = url;
  tempForm.target = name;
  const hideInputId = document.createElement('input');
  hideInputId.type = 'hidden';
  hideInputId.name = 'UserGuid';
  hideInputId.value = id;
  tempForm.appendChild(hideInputId);
  const hideInputSource = document.createElement('input');
  hideInputSource.type = 'hidden';
  hideInputSource.name = 'Source';
  hideInputSource.value = '2';
  tempForm.appendChild(hideInputSource);
  // tempForm.addEventListener('onsubmit', () => { openWindow(name); }, false);
  // tempForm.attachEvent('onsubmit', function(){ openWindow(name); });
  document.body.appendChild(tempForm);
  // tempForm.fireEvent('onsubmit');
  tempForm.submit();
  document.body.removeChild(tempForm);
}

@Component({
  components: {
    banner,
  },
})

export default class ActivityContent extends Vue {
  @State('userId') stateUserId!: string;
  @Getter('taskById', { namespace: 'task' }) taskById!: (id: string) => IActivityItem;
  @Getter('activityNotificationById', { namespace: 'notification' }) activityNotificationById!: (id: number) => IActivityItem;

  private isOpenIframe = false;

  get isTest() {
    return this.content.id === '1';
  }

  get isNotification() {
    return this.$route.query.process === 'notification';
  }

  onClose() {
    this.isOpenIframe = false;
  }

  nextExec(item: IActivityItem) {
    if (item.task !== 'action-prize') window.open(item.url, '_blank');
    else {
      openPostWindow(item.url, this.stateUserId, item.id);
      this.isOpenIframe = true;
    }
  }

  link() {
    if (this.isNotification) this.$router.push('/notification/main');
    else this.$router.push('/activity/main');
  }

  created() {
    const {
      id,
    } = this.$route.query;
    const idInt = parseInt(id.toString(), 10);

    if (id) {
      this.content = this.isNotification ? this.activityNotificationById(idInt) : this.taskById(id.toString());
    }
  }

  content = {
    id: '1',
    title: '標題標題標題標題標題1',
    start: '2020/8/1 上午 12:00',
    end: '2020/8/31 下午 23:59',
  };
}
